import React, { createContext, useState, useMemo, useEffect } from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';

export const ThemeContext = createContext();

const ThemeProviderWrapper = ({ children }) => {
    const [theme, setTheme] = useState(() => {
        const storedTheme = localStorage.getItem('theme');
        return ['light', 'dark', 'pink', 'blue', 'green'].includes(storedTheme) ? storedTheme : 'light';
    });

    useEffect(() => {
        localStorage.setItem("theme", theme);
    }, [theme, setTheme]);

    const lightTheme = useMemo(() => createTheme({
        palette: {
            primary: {
                lighter: '#a092ef',
                main: '#675bac',
                darker: '#37358f'
            },
            secondary: {
                main: '#5b78ac',
                lighter: '#ac5ba0',
                darker: '#905bac'
            },
            mode: "light",
        },
    }), []);

    const darkTheme = useMemo(() => createTheme({
        palette: {
            primary: {
                lighter: '#c9c4e1',
                main: '#8177ba',
                darker: '#433d99'
            },
            secondary: {
                main: '#7990ba',
                lighter: '#bc76b1',
                darker: '#bc76b1'
            },
            mode: "dark",
        },
    }), []);

    const pinkTheme = useMemo(() => createTheme({
        palette: {
            primary: {
                lighter: '#e6b8d2',
                main: '#f4a2d3',
                darker: '#913d6e'
            },
            secondary: {
                main: '#a86d8a',
                lighter: '#a55797',
                darker: '#983873'
            },
            mode: "dark",
            background: {
                default: '#510c36', // Dark blue background
                paper: '#781c55'   // Slightly lighter dark blue for card-like components
            },
            text: {
                primary: '#FFFFFF', // White text for contrast
                secondary: '#b381a0', // Lighter gray for secondary text
            },
        },
    }), []);

    const blueTheme = useMemo(() => createTheme({
        palette: {
            primary: {
                lighter: '#a5adcc',
                main: '#5f6993',
                darker: '#233677'
            },
            secondary: {
                main: '#172968',
                lighter: '#2c3d75',
                darker: '#767cbc'
            },
            mode: "dark",
            background: {
                default: '#091343', // Dark blue background
                paper: '#172968'   // Slightly lighter dark blue for card-like components
            },
            text: {
                primary: '#FFFFFF', // White text for contrast
                secondary: '#6f7bc6', // Lighter gray for secondary text
            },
        },
    }), []);

    const greenTheme = useMemo(() => createTheme({
        palette: {
            primary: {
                lighter: '#194308',
                main: '#873e35',
                darker: '#95742e'
            },
            secondary: {
                main: '#1c6a4f',
                lighter: '#288362',
                darker: '#12553e'
            },
            mode: "dark",
            background: {
                default: '#1d3227',
                paper: '#354a3f'
            },
            text: {
                primary: '#f6f4d7',
                secondary: '#bdb484',
            },
        },
    }), []);



    const selectedTheme = theme === 'light'
        ? lightTheme
        : theme === 'dark'
            ? darkTheme
            : theme === 'pink'
                ? pinkTheme
                : theme === 'blue'
                    ? blueTheme
                    : greenTheme;

    const toggleTheme = () => {
        setTheme((prevTheme) => {
            switch (prevTheme) {
                case 'light':
                    return 'dark';
                case 'dark':
                    return 'pink';
                case 'pink':
                    return 'blue';
                case 'blue':
                    return 'green';
                case 'green':
                default:
                    return 'light';
            }
        });
    };


    return (
        <ThemeContext.Provider value={{ theme, selectedTheme, toggleTheme, setTheme }}>
            <ThemeProvider theme={selectedTheme}>
                {children}
            </ThemeProvider>
        </ThemeContext.Provider>
    );
};

export default ThemeProviderWrapper;
